/*==============================
	Fonts
==============================*/

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&family=Rubik:ital,wght@0,300;0,400;0,500;1,400&display=swap");
/*==============================
	Common styles
==============================*/

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html,
body {
  height: 100%;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  background-color: #131720;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  transition: 0.5s ease;
  transition-property: color, background-color, border-color;
  cursor: pointer;
}

button:focus {
  outline: none;
}

a {
  transition: 0.5s ease;
  transition-property: color, background-color, border-color;
}

a:hover,
a:active,
a:focus {
  outline: none;
  text-decoration: none;
}

input,
textarea,
select {
  padding: 0;
  margin: 0;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-shadow: none;
  transition: 0.5s ease;
  transition-property: color, border-color;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

select::-ms-expand {
  display: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

::-moz-selection {
  background: #fff;
  color: #151f30;
  text-shadow: none;
}

::selection {
  background: #fff;
  color: #151f30;
  text-shadow: none;
}

::-webkit-input-placeholder {
  color: #f2f2f2;
  opacity: 1;
}

::-moz-placeholder {
  color: #f2f2f2;
  opacity: 1;
}

:-moz-placeholder {
  color: #f2f2f2;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #f2f2f2;
  opacity: 1;
}

:focus {
  outline: -webkit-focus-ring-color auto 0px;
}

.tab-content {
  width: 100%;
}

.tab-content > .tab-pane {
  display: none;
}

.tab-content > .active {
  display: block;
}

.fade {
  transition: opacity 0.4s linear;
}

@media screen and (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}

.fade:not(.show) {
  display: none;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.4s ease;
}

@media screen and (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}

/*==============================
	Header
==============================*/

/*==============================
	Sidebar
==============================*/

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: #131720;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 101;
  width: 280px;
  transform: translateX(-100%);
  transition: 0.5s ease;
  border-right: 1px solid rgba(47, 128, 237, 0.1);
}

.sidebar__logo {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 70px;
  padding: 0 30px;
  background-color: #131720;
  width: 100%;
  position: relative;
  border-bottom: 1px solid rgba(47, 128, 237, 0.1);
}

.sidebar__logo img {
  width: 80px;
  height: auto;
  display: block;
}

.sidebar__user {
  padding: 20px 30px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid rgba(47, 128, 237, 0.1);
}

.sidebar__user-img {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  overflow: hidden;
  display: block;
  margin-right: 15px;
}

.sidebar__user-img img {
  width: 100%;
}

.sidebar__user-title {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.sidebar__user-title p {
  color: #fff;
  display: block;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 0;
  line-height: 100%;
}

.sidebar__user-title span {
  display: block;
  color: #e0e0e0;
  font-size: 10px;
  line-height: 100%;
  margin-bottom: 5px;
}

.sidebar__user-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  border-radius: 10px;
  background-color: #151f30;
  margin-left: auto;
}

.sidebar__user-btn svg {
  fill: #2f80ed;
  width: 20px;
  height: auto;
  transition: fill 0.5s;
}

.sidebar__user-btn:hover {
  background-color: #fff;
}

.sidebar__user-btn:hover svg {
  fill: #151f30;
}

.sidebar__nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;
  padding: 30px 0;
  width: 100%;
  max-height: calc(100vh - 220px);
}

.sidebar__nav-link {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  color: #e0e0e0;
  font-family: "Rubik", sans-serif;
  margin-left: 30px;
}

.sidebar__nav-link svg {
  fill: #e0e0e0;
  width: 24px;
  height: auto;
  transition: fill 0.5s;
  margin-right: 15px;
  margin-bottom: 2px;
}

.sidebar__nav-link svg:last-child {
  width: 16px;
  margin-right: 0;
  margin-top: 2px;
  margin-left: 2px;
  margin-bottom: 0;
}

.sidebar__nav-link:hover {
  color: #2f80ed;
}

.sidebar__nav-link:hover svg {
  fill: #2f80ed;
}

.sidebar__nav-link--active,
.sidebar__nav-link[aria-expanded="true"] {
  color: #2f80ed;
}

.sidebar__nav-link--active svg,
.sidebar__nav-link[aria-expanded="true"] svg {
  fill: #2f80ed;
}

.sidebar__nav-link--active {
  cursor: default;
}

.sidebar__nav-item {
  width: 100%;
  display: block;
  margin-bottom: 20px;
  position: relative;
}

.sidebar__nav-item:last-child {
  margin-bottom: 0;
}

.sidebar__menu {
  padding: 0;
  display: block;
  width: 100%;
  min-width: 200px;
  text-align: left;
  margin-top: 15px;
  overflow: hidden;
  background-color: rgba(47, 128, 237, 0.1);
}

.sidebar__menu li {
  padding: 0 0 0 49px;
  margin-bottom: 15px;
  position: relative;
}

.sidebar__menu li:before {
  content: "";
  position: absolute;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #2f80ed;
  top: 50%;
  left: 30px;
  margin-top: -2px;
}

.sidebar__menu li:last-child {
  margin-bottom: 0;
  padding-bottom: 20px;
}

.sidebar__menu li:last-child:before {
  top: calc(50% - 10px);
}

.sidebar__menu li:first-child {
  padding-top: 20px;
}

.sidebar__menu li:first-child:before {
  top: calc(50% + 10px);
}

.sidebar__menu a {
  font-size: 14px;
  color: #e0e0e0;
  display: block;
  font-weight: 400;
}

.sidebar__menu a:hover {
  color: #2f80ed;
}

.sidebar__menu a.active {
  color: #2f80ed;
  cursor: default;
}

.sidebar__copyright {
  margin-top: auto;
  padding: 0 30px 20px;
  color: #e0e0e0;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
}

.sidebar__copyright a {
  color: #fff;
}

.sidebar__copyright a:hover {
  color: #2f80ed;
}

.sidebar--active {
  transform: translateX(0);
}

@media (min-width: 1200px) {
  .sidebar {
    transform: translateX(0);
  }
  .sidebar__logo {
    height: 80px;
  }
}

/*==============================
	Main
==============================*/

.main {
  position: relative;
  margin-top: 70px;
  padding: 20px 0 0;
}

.main__title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(47, 128, 237, 0.1);
  padding-bottom: 20px;
}

.main__title h2 {
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  color: #fff;
  font-size: 28px;
  line-height: 100%;
  margin-bottom: 0;
}

.main__title h2 b {
  font-weight: 500;
}

.main__title-stat {
  color: #e0e0e0;
  font-size: 14px;
  margin-left: 15px;
  margin-top: 10px;
}

.main__title-wrap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: auto;
  width: 100%;
  margin-top: 20px;
}

.main__title-form {
  position: relative;
  margin-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.main__title-form input {
  height: 40px;
  border-radius: 16px;
  background-color: #151f30;
  border: none;
  padding: 0 60px 0 20px;
  font-size: 14px;
  color: #fff;
  width: 100%;
  position: relative;
  z-index: 1;
}

.main__title-form button {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  right: 20px;
  z-index: 2;
}

.main__title-form button svg {
  width: 16px;
  height: auto;
}

.main__title-link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 40px;
  border-radius: 16px;
  background-color: #2f80ed;
  font-size: 14px;
  color: #e0e0e0;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: auto;
}

.main__title-link:hover {
  color: #151f30;
  background-color: #fff;
}

.main__table-wrap {
  position: relative;
  width: 100%;
  overflow: auto;
}

.main__table {
  width: 100%;
  min-width: 1050px;
  border-spacing: 0;
}

.main__table thead {
  border-bottom: 20px solid #131720;
}

.main__table thead th {
  font-size: 12px;
  color: #e0e0e0;
  font-weight: 400;
  padding: 0 20px 0 20px;
  line-height: 100%;
  margin-bottom: 0;
  border: none;
}

.main__table tbody tr {
  border-bottom: 10px solid #131720;
}

.main__table tbody td {
  padding: 0;
  background-color: transparent;
  border: 0px solid transparent;
}

.main__table tbody td:first-child .main__table-text {
  border-radius: 16px 0 0 16px;
}

.main__table tbody td:last-child .main__table-text,
.main__table tbody td:last-child .main__table-btns {
  border-radius: 0 16px 16px 0;
}

.main__table--dash {
  width: 100%;
  min-width: 500px;
}

.main__table--dash thead {
  border-bottom: 1px solid rgba(47, 128, 237, 0.1);
}

.main__table--dash thead th {
  padding: 0 10px 15px 0;
}

.main__table--dash tbody tr {
  border-bottom: none;
  background-color: transparent;
}

.main__table--dash tbody td {
  padding: 0;
}

.main__table--dash tbody td .main__table-text {
  background-color: transparent;
  padding: 10px 15px 0 0;
}

.main__table-text {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  line-height: 30px;
  padding: 20px;
  background-color: #151f30;
  white-space: nowrap;
}

.main__table-text a {
  color: #fff;
}

.main__table-text a:hover {
  color: #2f80ed;
}

.main__table-text svg {
  margin-right: 6px;
  fill: #2f80ed;
  width: 16px;
  height: auto;
}

.main__table-text--rate {
  font-weight: 500;
  font-size: 14px;
}

.main__table-text--red {
  color: #eb5757;
}

.main__table-text--green {
  color: #29b474;
}

.main__table-text--grey {
  color: rgba(255, 255, 255, 0.5);
}

.main__table-btns {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
  background-color: #151f30;
}

.main__table-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  font-size: 16px;
  color: #fff;
  border-radius: 10px;
  background-color: #131720;
  margin-right: 10px;
}

.main__table-btn svg {
  width: 20px;
  height: auto;
}

.main__table-btn:last-child {
  margin-right: 0;
}

.main__table-btn--delete {
  background-color: rgba(235, 87, 87, 0.1);
}

.main__table-btn--delete svg {
  fill: #eb5757;
}

.main__table-btn--delete:hover {
  background-color: rgba(235, 87, 87, 0.2);
}

.main__table-btn--edit {
  background-color: rgba(47, 128, 237, 0.1);
}

.main__table-btn--edit svg {
  fill: #2f80ed;
}

.main__table-btn--edit:hover {
  background-color: rgba(47, 128, 237, 0.2);
}

.main__table-btn--banned {
  background-color: rgba(41, 180, 116, 0.1);
}

.main__table-btn--banned svg {
  fill: #29b474;
}

.main__table-btn--banned:hover {
  background-color: rgba(41, 180, 116, 0.2);
}

.main__table-btn--view {
  background-color: rgba(255, 195, 18, 0.1);
}

.main__table-btn--view svg {
  fill: #ffc312;
}

.main__table-btn--view:hover {
  background-color: rgba(255, 195, 18, 0.2);
}

.main__user {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: #151f30;
  padding: 0 20px;
  height: 70px;
}

.main__meta {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 40px;
}

.main__meta h3 {
  color: #fff;
  display: block;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 0;
  line-height: 20px;
}

.main__meta span {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  line-height: 20px;
}

.main__avatar {
  display: block;
  position: relative;
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 10px;
  margin-right: 15px;
}

.main__avatar img {
  width: 100%;
}

@media (min-width: 576px) {
  .main {
    padding: 30px 0 0;
  }
  .main__title {
    margin-bottom: 30px;
    padding-bottom: 30px;
  }
  .main__title h2 {
    font-size: 30px;
  }
  .main__title-wrap {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .main__title-form {
    margin-top: 0;
    width: 300px;
  }
}

@media (min-width: 768px) {
  .main__title-wrap {
    width: auto;
    margin-top: 0;
  }
}

@media (min-width: 1200px) {
  .main {
    padding: 0 15px 30px 295px;
    margin-top: 0;
  }
  .main__title {
    padding-bottom: 0;
    height: 80px;
  }
}

/*==============================
	Stats
==============================*/

.stats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 16px;
  background-color: #151f30;
  width: 100%;
}

.stats span {
  font-size: 16px;
  line-height: 100%;
  font-weight: 400;
  color: #fff;
  margin-bottom: 0;
}

.stats p {
  font-size: 30px;
  line-height: 100%;
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  color: #e0e0e0;
  margin-bottom: 0;
  margin-top: 15px;
}

.stats p a {
  color: #fff;
}

.stats p a:hover {
  color: #2f80ed;
}

.stats svg {
  position: absolute;
  bottom: 20px;
  right: 20px;
  fill: #2f80ed;
  height: 34px;
  width: auto;
}

@media (min-width: 576px) {
  .stats {
    margin-bottom: 30px;
  }
}

/*==============================
	Dashbox
==============================*/

.dashbox {
  background-color: #151f30;
  margin-bottom: 20px;
  border-radius: 16px;
  overflow: hidden;
}

.dashbox__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid rgba(47, 128, 237, 0.1);
}

.dashbox__title h3 {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  color: #fff;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  margin-bottom: 0;
  font-family: "Rubik", sans-serif;
  font-weight: 400;
}

.dashbox__title h3 svg {
  fill: #2f80ed;
  margin-right: 10px;
  width: 24px;
  height: auto;
}

.dashbox__wrap {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.dashbox__more {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  height: 30px;
  background-color: #131720;
  font-size: 12px;
  margin-left: 15px;
  color: #e0e0e0;
  border-radius: 12px;
}

.dashbox__more:hover {
  color: #2f80ed;
}

.dashbox__refresh {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 30px;
}

.dashbox__refresh svg {
  fill: #e0e0e0;
  width: 16px;
  height: auto;
  transition: fill 0.5s;
}

.dashbox__refresh:hover svg {
  fill: #2f80ed;
}

.dashbox__table-wrap {
  position: relative;
  display: block;
  width: 100%;
  overflow: auto;
  padding: 20px 20px 15px;
}

@media (min-width: 576px) {
  .dashbox {
    margin-bottom: 30px;
  }
}

/*==============================
	Filter
==============================*/

.filter {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.filter__item-label {
  font-size: 12px;
  display: block;
  color: #e0e0e0;
  font-weight: 400;
  line-height: 10px;
}

.filter__item-btn {
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  height: 30px;
  transition: 0.5s;
}

.filter__item-btn input {
  display: block;
  position: relative;
  width: auto;
  background-color: transparent;
  border: none;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  height: 30px;
  margin-right: 10px;
}

.filter__item-btn span {
  position: relative;
  width: 16px;
  height: 16px;
  display: block;
  margin-top: 3px;
}

.filter__item-btn span:before,
.filter__item-btn span:after {
  content: "";
  position: absolute;
  display: block;
  width: 16px;
  height: 2px;
  background-color: #e0e0e0;
  left: 0;
  top: 50%;
  transition: 0.5s;
  margin-top: -5px;
  border-radius: 2px;
}

.filter__item-btn span:after {
  margin-top: 2px;
  width: 10px;
}

.filter__item-btn[aria-expanded="true"] span:before {
  transform: rotate(45deg);
  margin-top: -2px;
  background-color: #2f80ed;
}

.filter__item-btn[aria-expanded="true"] span:after {
  margin-top: -2px;
  width: 16px;
  transform: rotate(-45deg);
  background-color: #2f80ed;
}

.filter__item-btn:hover span:before,
.filter__item-btn:hover span:after {
  background-color: #2f80ed;
}

.filter__item-menu {
  top: 100%;
  left: 0;
  margin-top: 0;
  width: 200px;
  padding: 0 20px;
  background-color: #151f30;
  display: block;
  transition: 0.5s;
  position: absolute;
  z-index: 100;
  pointer-events: none;
  opacity: 0;
  border-radius: 16px;
}

.filter__item-menu li {
  position: relative;
  margin-bottom: 15px;
  color: #e0e0e0;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  transition: color 0.5s;
}

.filter__item-menu li:hover {
  color: #2f80ed;
}

.filter__item-menu li:first-child {
  padding-top: 20px;
}

