.profile__mobile-tabs {
  position: relative;
  margin-bottom: -10px;
}

.profile__mobile-tabs-btn {
  display: inline-flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  height: 30px;
  transition: 0.5s;
}

.profile__mobile-tabs-btn input {
  display: block;
  position: relative;
  width: auto;
  background-color: transparent;
  border: none;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  height: 30px;
  margin-right: 10px;
}

.profile__mobile-tabs-btn span {
  position: relative;
  width: 16px;
  height: 16px;
  display: block;
  margin-top: 3px;
}

.profile__mobile-tabs-btn span:before,
.profile__mobile-tabs-btn span:after {
  content: "";
  position: absolute;
  display: block;
  width: 16px;
  height: 2px;
  background-color: #e0e0e0;
  left: 0;
  top: 50%;
  transition: 0.5s;
  margin-top: -5px;
  border-radius: 2px;
}

.profile__mobile-tabs-btn span:after {
  margin-top: 2px;
  width: 10px;
}

.profile__mobile-tabs-btn[aria-expanded="true"] span:before {
  transform: rotate(45deg);
  margin-top: -2px;
  background-color: #2f80ed;
}

.profile__mobile-tabs-btn[aria-expanded="true"] span:after {
  margin-top: -2px;
  width: 16px;
  transform: rotate(-45deg);
  background-color: #2f80ed;
}

.profile__mobile-tabs-btn:hover span:before,
.profile__mobile-tabs-btn:hover span:after {
  background-color: #2f80ed;
}

.profile__mobile-tabs-menu {
  top: 100%;
  left: 0;
  margin-top: -10px;
  width: 200px;
  padding: 0 20px;
  background-color: #151f30;
  display: block;
