@media (min-width: 768px) {
  .profile__content {
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 0 20px;
    height: 80px;
  }
  .profile__user {
    width: auto;
    margin-bottom: 0;
    margin-right: 40px;
  }
  .profile__tabs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    overflow: hidden;
  }
  .profile__tabs li {
    margin-right: 30px;
  }
  .profile__tabs li:last-child {
    margin-right: 0;
  }
  .profile__tabs div {
    display: block;
    line-height: 80px;
    color: #fff;
    position: relative;
    font-size: 14px;
  }
  .profile__tabs a:hover {
    color: #2f80ed;
  }
  .profile__tabs a:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    display: block;
    background-color: #2f80ed;
    transition: 0.5s;
    transform: translateY(2px);
    opacity: 0;
  }
  .profile__tabs a.active {
    color: #2f80ed;
  }
  .profile__tabs a.active:before {
    opacity: 1;
    transform: translateY(0);
  }
  .profile__mobile-tabs {
    display: none;
  }
  .profile__actions {
    position: relative;
    bottom: auto;
    right: auto;
    margin-left: auto;
  }
  .profile__action {
    margin-right: 15px;
  }
  .profile__action:last-child {
    margin-right: 0;
  }
