/*==============================
	Modal
==============================*/

.modal {
  position: relative;
  display: block;
  background-color: #151f30;
  margin: 40px auto;
  width: 100%;
  max-width: 420px;
  padding: 20px;
  border-radius: 16px;
  overflow: hidden;
}

.modal .mfp-close {
  display: none;
}

.modal__title {
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  color: #fff;
  font-size: 28px;
  line-height: 100%;
  margin-bottom: 15px;
  text-align: center;
  display: block;
}

.modal__text {
  font-size: 14px;
  line-height: 24px;
  color: #e0e0e0;
  font-weight: 400;
  display: block;
  text-align: center;
  margin-bottom: 0;
}

.modal__btns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.modal__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: calc(50% - 10px);
  border-radius: 16px;
  font-size: 14px;
  color: #e0e0e0;
  text-transform: uppercase;
  font-weight: 500;
}

.modal__btn--apply {
