}

.modal__btn--apply:hover {
  color: #29b474;
  background-color: #fff;
}

.modal__btn--dismiss {
  background-color: #fff;
  color: #151f30;
}

.modal__btn--dismiss:hover {
  color: #eb5757;
  background-color: #fff;
}

.modal--view {
  padding: 20px 0 0 0;
  max-width: 700px;
}

@media (min-width: 768px) {
